* {
  padding: 0;
  margin: 0;
}

.App {
  height: 100vh;
  width: 100vw;
  overflow: auto;
}

.replying {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  column-gap: 2cm;
}

.close {
  width: .7cm;
  height: .8cm;
}

.chatParent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 0.1cm;
}

.chatBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.mainChat {
  display: flex;
  flex-direction: column;
  border: thin solid rgb(157, 157, 157);
  row-gap: 0.2cm;
  border-radius: 6px;
  height: 12cm;
  width: 80vw;
  overflow: auto;
  scroll-behavior: smooth;
  padding-bottom: 0.2cm;
  padding-left: 0.2cm;
}

.userImg {
  width: 1cm;
  height: 1cm;
  border-radius: 50%;
}

.nameDiv {
  background-color: rgb(147, 234, 226);
  width: fit-content;
  padding: 0.1cm 0.3cm;
  border-radius: 6px;

}

.userName {
  font-size: 12px;
  color: rgb(91, 91, 91);
}

.leftMsg {
  display: flex;
  flex-direction: row;
  column-gap: 0.2cm;
  margin-top: 0.2cm;


}

.left {
  text-align: left;
  /* padding:0.5cm; */
}

.replymsgContainerRight {
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: end;
  column-gap: 0.1cm;

  &:hover {
    .icon1 {
      display: flex;
    }
  }

}

.repliedBoxRight {
  background-color: rgba(235, 199, 255, 0.956);
  padding: 0.1cm 0.2cm;
  border-radius: 12px;


}

.repliedToRight {
  background-color: rgba(218, 152, 254, 0.956);
  padding: 0.1cm 0.2cm;
  border-radius: 12px;
  border: 2px solid rgb(198, 127, 255);
}

.replymsgContainerLeft {
  background-color: rgb(255, 255, 255);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: end;
  column-gap: 0.1cm;

  &:hover {
    .icon1 {
      display: flex;
    }
  }

}

.repliedBoxLeft {
  background-color: rgba(235, 199, 255, 0.956);
  padding: 0.1cm 0.2cm;
  border-radius: 12px;


}

.repliedToLeft {
  background-color: rgba(218, 152, 254, 0.956);
  padding: 0.1cm 0.2cm;
  border-radius: 12px;
  border: 2px solid rgb(198, 127, 255);
}

.right {
  // text-align: right;
  background-color: rgb(238, 242, 159);
  width: fit-content;
  padding: 0.1cm 0.3cm;
  border-radius: 6px;
}

.rightMsg {
  margin: 0.1cm;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
}

.msg {
  font-size: 14px;
}

.icon {
  width: 0.7cm;
  height: 0.7cm;
}

.inputField,
.basic {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 0.2cm;
}

.replyField {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 0.2cm;

}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.4cm 1cm;
}

.signout {
  height: 1cm;
  width: 1cm;
  cursor: pointer;
}

.msgContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;

  &:hover {
    .icon1 {
      display: flex;
    }
  }
}

.tick {
  width: 0.3cm;
  height: 0.3cm;
}

.inputText {
  width: 20cm;
  padding: 0.3cm;
}

.icon1 {
  height: 0.5cm;
  width: 0.5cm;
  cursor: pointer;
  display: none;
}

.lmessage {
  display: flex;
  flex-direction: row;
  column-gap: 0.2cm;
  align-items: center;

  &:hover {
    .icon1 {
      display: flex;
    }
  }
}

.HomePage {
  width: 100vw;
  height: 95vh;
}

.mainContent {
  display: flex;
  flex-direction: row;
  justify-content: center;
  column-gap: 3cm;
  row-gap: 0.8cm;
  align-items: center;
  flex-wrap: wrap;
  padding: 1cm;
}

.mainLogo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 0.2cm;
}

.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 1cm;
  border: thin solid rgb(183, 183, 183);
  box-shadow: 1px 2px 0.5px grey;
  border-radius: 12px;
  width: fit-content;
  padding: 1cm;
}

.chatImage {
  width: 9cm;
  height: 9cm;
}

.SignInButton {
  padding: 0.2cm 0.3cm;
  background-color: rgb(48, 125, 250);
  color: #ffffff;
  box-shadow: 0.5px 0.5px black;
  border: none;
  border-radius: 6px;
}

.logo,
.userDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 0.3cm;
}


.userNameNav {
  font-size: 16px;
}

.heading {
  font-size: 20px;
  font-weight: bolder;
}

.headingMain {
  font-size: 20px;
  font-weight: bolder;
}

.ChatPage {
  height: 100vh;
}

@media only screen and (min-width:940px) and (max-width:1040px) {
  .inputText {
    width: 18cm;
  }

  .mainContent {
    column-gap: 2cm;
  }
}

@media only screen and (min-width:840px) and (max-width:940px) {
  .inputText {
    width: 15cm;
  }

  .mainContent {
    column-gap: 1cm;
  }

  .chatImage {
    height: 8cm;
    width: 8cm;
  }
}

@media only screen and (min-width:680px) and (max-width:840px) {
  .inputText {
    width: 12cm;
  }

  .chatImage {
    height: 7cm;
    width: 7cm;
  }
}

@media only screen and (min-width:560px) and (max-width:680px) {
  .inputText {
    width: 10cm;
  }

  .msg {
    font-size: 13px;
  }

  .chatImage {
    height: 6cm;
    width: 6cm;
  }
}

@media only screen and (min-width:450px) and (max-width:560px) {
  .inputText {
    width: 8cm;
  }

  .nav {
    padding: 0.3cm 0.5cm;
  }

  .msg {
    font-size: 12px;
  }

  .chatImage {
    height: 5cm;
    width: 5cm;
  }
}

@media only screen and (min-width:370px) and (max-width:450px) {
  .inputText {
    width: 6cm;
    padding: 0.2cm 0.3cm;
  }

  .heading {
    display: none;
  }

  .userNameNav {
    display: none;
  }

  .userImg,
  .signout {
    height: 0.8cm;
    width: 0.8cm;
  }

  .icon {
    width: 0.5cm;
    height: 0.5cm;
  }

  .nav {
    padding: 0.2cm 0.4cm;
  }

  .mainChat {
    height: 12cm;
  }

  .msg {
    font-size: 11px;
  }

  .chatImage {
    height: 4cm;
    width: 4cm;
  }

  .headingMain {
    font-size: 20px;
  }

  .login {
    padding: 0.7cm;
  }
}

@media only screen and (max-width:370px) {
  .inputText {
    width: 4cm;
    padding: 0.2cm;
  }

  .heading {
    display: none;
  }

  .userNameNav {
    display: none;
  }

  .userImg,
  .signout {
    height: 0.8cm;
    width: 0.8cm;
  }

  .icon {
    width: 0.5cm;
    height: 0.5cm;
  }

  .nav {
    padding: 0.2cm 0.2cm;
  }

  .mainChat {
    height: 10cm;
  }

  .msg {
    font-size: 11px;
  }

  .chatImage {
    height: 4cm;
    width: 4cm;
  }

  .headingMain {
    font-size: 16px;
  }

  .login {
    padding: 0.5cm;
  }
}